import uniCombox from '@/components/uni-combox/uni-combox';
import uniIcons from '@/components/uni-icons/uni-icons.vue';
import http from '@/common/request.js';

export default {
	components: { uniCombox, uniIcons },

	created() {
	
		this.reloadTerms();		

		this.reloadCourseType();
	},
	data() {
		return {
			
			selectCourseTypeIndex: 0,
			selectTermIndex: 0,
			list_course: [],
			list_term: [
				{
					SemesterName: '全部学期'
				}
			],

			list_course_type: [
				{
					CourseClassName: '全部课程'
				}
			]
		};
	},
	methods: {
		// 刷新课程列表
		reloadCourseItem() {
			let that = this;

			http.request({
				url: '/api/Student/GetMyCourseList',
				method:"POST",
				data:{
					semesterId: "",
					pageIndex: 1,
					orderByPostTime: 2,
					orderByScanCount: 0,
					orderByPraiseCount: 0, 
					orderByCollectCount: 0,
					isPublished: 1,
					courseClassId: "",
					classType: 0
				}
			}).then(r => {
				r = that.fr(r); 
				
				console.log(JSON.stringify(r))
				if (r.Code == 200) {
					that.list_course=r.Data || []
				}
			});
		},
		// 刷新课程信息
		reloadCourseType() {
			let that = this;

			http.request({
				url: '/api/Common/GetOpenCourseConfig'
			})
				.then(r => {
					r = r[1].data;
					console.log('返回值：', JSON.stringify(r));

					if (r.Code == 200) {
						that.list_course_type = r.Data || [];
					}
				})
				.catch(e => {
					console.log('error:', e);
				});
		},
		// 刷新学期信息
		reloadTerms() {
			let that = this;
			http.request({
				url: '/api/Common/GetSemester'
			}).then(r => {
				r = that.fr(r);
				if (r.Code == 200) {
					that.list_term = r.Data;
				}
			});
		},
		// 修改选中的课程
		courseTypeChange(e) {
			this.reloadCourseItem();
		},

		// 修改学期
		termChange(e) {
			this.reloadCourseItem();
		},
		bindMultiPickerColumnChange: function(e) {
			console.log(e);
			return false;
			console.log('修改的列为：' + e.detail.column + '，值为：' + e.detail.value);
			this.multiIndex[e.detail.column] = e.detail.value;
			switch (e.detail.column) {
				case 0: //拖动第1列
					switch (this.multiIndex[0]) {
						case 0:
							this.multiArray[1] = ['中国', '日本'];
							this.multiArray[2] = ['北京', '上海', '广州'];
							break;
						case 1:
							this.multiArray[1] = ['英国', '法国'];
							this.multiArray[2] = ['伦敦', '曼彻斯特'];
							break;
					}
					this.multiIndex.splice(1, 1, 0);
					this.multiIndex.splice(2, 1, 0);
					break;
				case 1: //拖动第2列
					switch (
						this.multiIndex[0] //判断第一列是什么
					) {
						case 0:
							switch (this.multiIndex[1]) {
								case 0:
									this.multiArray[2] = ['北京', '上海', '广州'];
									break;
								case 1:
									this.multiArray[2] = ['东京', '北海道'];
									break;
							}
							break;
						case 1:
							switch (this.multiIndex[1]) {
								case 0:
									this.multiArray[2] = ['伦敦', '曼彻斯特'];
									break;
								case 1:
									this.multiArray[2] = ['巴黎', '马赛'];
									break;
							}
							break;
					}
					this.multiIndex.splice(2, 1, 0);
					break;
			}
			this.$forceUpdate();
		},
		toCourseDetail() {
			uni.navigateTo({
				url: '/pages/students/course/courseDetail/courseDetail'
			});
		},
		testAxios() {
			uni.request({
				url: '/api/Common/GetOpenCourseConfig',
				success: r => {
					console.log(r);
				}
			});
		}
	}
};
